





























import Vue from 'vue'
import Component from 'vue-class-component'
import { Getter, Action } from 'vuex-class'
import CreateForm from '@/views/system-configuration/menu-management/create/index.vue'
import EditForm from '@/views/system-configuration/menu-management/edit/index.vue'
import mButton from '@/views/system-configuration/menu-management/button.vue'
import mType from '@/views/system-configuration/menu-management/type.vue'
@Component({
    components: { CreateForm, EditForm, mButton },
})
export default class App extends Vue {
    $refs: {
        checkboxElement: HTMLFormElement
    }

    defaultData = []

    modal1 = false
    modal2 = false
    index = 0
    // 初始化数据
    columns1= [
        {
            type:'index',
            title: '序号',
            key: 'name',
            width: 100,
            align: 'center',
        },
       
        {
            title: '菜单名称',
            key: 'resource_name',
        },
         {
            title: '菜单图标',
            key: 'icon',
            align: 'center',
            render: (h, params) => {
                return h('Icon', {
                    props: {
                        'type' : params.row.icon,
                        'size' : 16
                    }
                })
            }
        },
        {
            title: '排序',
            width: 100,
            key: 'resource_order',
            align: 'center',
        },
        {
            title: '菜单URL',
            key: 'resource_url',
            width: 700,
          
        },
        {
            title: '类型',
            key: 'type',
            align: 'center',
            render: (h, params) =>{
                return h (mType, {
                    props: {
                        row: params.row
                    }
                })
            }

        },
        {
            title: '级别',
            key: 'level',
            width: 70,
            align: 'center',
        },
        {
            title: '状态',
            key: 'status',
            align: 'center',
        },
        {
            title: '操作',
            render: (h, params) => {
                return h(mButton, {
                    props: {
                        row: params.row
                    },
                    on: {
                        'removeSend' : (val) => {
                            this.handRemove(val)
                        },
                        'showSend' : (val) => {
                            this.handShow(val)
                        }
                    }
                })
            }

        }
        ]
    ok() {
    }
    cancel() {
    }

    /**
     * 表单编辑
     * @param index
     */
    handShow(index) {
        // 优化值被改变的动作
           setTimeout(()=>{
               this.modal2 = true;
           },300)
           this.index = index;
           this.$refs['refResetEdit'].handleSetDefaultData()
    }

    /**
     * 删除
     * @param value
     */
    handRemove(value) {
        console.log(value,'val')
        // TODO 删除
        // new Vue.prototype.$http('MENU', {}, value).delete('menu').then(res=>{
        //     if(res) this.defaultList()
        // })
    }

    /**
     * 创建
     */
    handleCreateForm() {
        this.modal1 = true;
        this.$refs['refResetCreate'].handleSetDefaultData()
    }

    defaultList() {
       new Vue.prototype.$http('MENU').get('menu').then(res=>{
           console.log(res, 'res')
           if(res) this.defaultData = res
        })
    }

    /**
     * 重写
     */
    reDefaultList() {
        new Vue.prototype.$http('RE').get('menu').then(res=>{
          if(res) this.defaultData = res
            console.log(res, 'res')
        })
    }

    /**
     * 清空
     */
    handleReset() {
        Vue.prototype.$Message.success('操作成功!');
        this.modal1 = this.modal2 = false
        this.reDefaultList()
    }

    created() {
        // this.defaultList()

        this.reDefaultList()
    }

}



