import { Getter, Action } from 'vuex-class'
import {Component, Prop, Emit} from 'vue-property-decorator'

@Component({

    watch:{
        // 解决异步传参
        defaultFormData(defaultData){
            let obj = {};

            if(defaultData.type === 1) {
                this.type = 1
                this.menuName = 'menu'
                for(let v in this.formValidateMenu) {
                    this.formValidateMenu[v] = defaultData[v]
                }
            }

            if(defaultData.type === 2) {
                this.type = 2
                this.menuName = 'node'
                for(let val in this.formValidateNode) {
                    this.formValidateNode[val] = defaultData[val]
                }
            }
            
        },
        defaultData(value) {
            // console.log(value, 'from')
            this.defaultData = value
        }
    },
})
export default class App extends Vue {

    @Getter menus

    @Prop({default: {}})
    defaultFormData: Object

    @Prop()
    defaultData: Object

    $refs: {
        checkboxElement: HTMLFormElement
    }


    type = 1
    menuName = ''


    formValidate = {
        resource_name: '',
        resource_url: '',
        resource_parent_id: '0',
        resource_order: '',
        icon:'',
        type: 1,
        remark:''
    }

     formValidateMenu = {
        resource_name: '',
        resource_url: '',
        resource_parent_id: '0',
        resource_order: '',
        icon:'',
        type: 1,
        remark:''
    }

     formValidateNode = {
        node_name: '',
        node_url: '',
        menu_id: '',
        node_order: '',
        node_type: 2,
        node_remark:''
    }


    typeList = [
        {
            id: 1,
            name: '菜单'
        },
        {
            id: 2,
            name: '按钮'
        }
    ]

    menuDefaults = []
    nodeDefaults = []


    created() {
         new Vue.prototype.$http('RE').get('default').then(res=>{
            this.menuDefaults = res.menu
            this.nodeDefaults = res.node
        })
        console.log('ddd')
        // console.log(this.$parent.$parent,'parent')
    }

    /**
     * 菜单验证规则
     */
    ruleValidate = {
        resource_name: [
            { required: true, message: '请输入菜单名称' }
        ],
        resource_url: [
             { required: true, message: '请输入url' }
        ]
    }

     ruleValidateNode = {
        menu_id: [
            { required: true, message: '请选择归属菜单' }
        ],
        node_name: [
            { required: true, message: '请输入按钮名称' }
        ],
        node_url: [
            { required: true, message: '请输入url' }
        ]
    }

    /**
     * 表单数据提交到父组件
     * @param name
     */
    handleSubmit (name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
                // console.log(this.formValidate, '表单数据')
                this.$emit('getFormData', this.formValidate)
                // Vue.prototype.$Message.success('Success!');
            } else {
                Vue.prototype.$Message.error('表单内容不能为空！');
            }
        })
    }

    /**
     * 表单 重置
     * @param name
     */
    handleReset (name) {
        this.$refs[name].resetFields();
    }



    /**
     * 表单数据提交到父组件
     * @param name
     */
    handleSubmitMenu (name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
                // console.log(this.formValidate, '表单数据')
                this.$emit('getFormDataMenu', this.formValidateMenu)
                // Vue.prototype.$Message.success('Success!');
            } else {
                Vue.prototype.$Message.error('表单内容不能为空！');
            }
        })
    }

      /**
     * 表单数据提交到父组件
     * @param name
     */
    handleSubmitNode (name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
                // console.log(this.formValidate, '表单数据')
                this.$emit('getFormDataNode', this.formValidateNode)
                // Vue.prototype.$Message.success('Success!');
            } else {
                Vue.prototype.$Message.error('表单内容不能为空！');
            }
        })
    }


    /**
