     * @param name
     */
    handleResetMenu (name) {
        this.$refs[name].resetFields();
    }

     /**
     * 表单 重置
     * @param name
     */
    handleResetNode (name) {
        this.$refs[name].resetFields();
    }





    clickChange(nowPage) {
        console.log(nowPage)
    }
}



